import React, { Component } from "react";
import "./App.css";
import SearchBar from "./components/SearchBar/SearchBar";
import Playlist from "./components/Playlist/Playlist";
import SearchResults from "./components/SearchResults/SearchResults";
import Spotify from "./util/Spodify";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchResults: [],
      playlistName: "RealMusic",
      playlistTracks: [
        /* {
          id: 4,
          name: "Why",
          artist: "NF",
          album: "Too Many Faces"
        },
        {
          id: 5,
          name: "Warm up",
          artist: "NF",
          album: "Perception"
        },
        {
          id: 6,
          name: "Outro",
          artist: "NF",
          album: "Perception"
        }*/
      ]
    };
    this.addTrack = this.addTrack.bind(this);
    this.removeTrack = this.removeTrack.bind(this);
    this.updatePlaylistName = this.updatePlaylistName.bind(this);
    this.savePlaylist = this.savePlaylist.bind(this);
    this.search = this.search.bind(this);
  }

  addTrack(track) {
    if (
      this.state.playlistTracks.find(savedTrack => savedTrack.id !== track.id)
    ) {
      this.setState({
        playlistTracks: [...this.state.playlistTracks, track]
      });
    }
  }

  removeTrack(track) {
    let tracks = this.state.playlistTracks;
    tracks = this.state.playlistTracks.filter(
      current => current.id !== track.id
    );
    this.setState({
      playlistTracks: tracks
    });
  }

  updatePlaylistName(name) {
    this.setState({
      playlistName: name
    });
  }

  savePlaylist() {
    const playlistUrls = this.state.playlistTracks.map(track => track.uri);
    Spotify.savePlaylist(this.state.playlistName, playlistUrls.uri).then(
      response => {
        if (response) {
          this.setState({
            playlistName: "New Playlist"
          });
          this.setState({
            playlistTracks: []
          });
        }
      }
    );
  }

  search(term) {
    const searchNew = Spotify.search(term);
    this.setState({ searchResults: searchNew });
  }

  render() {
    return (
      <div>
        <h1>
          Ja<span className="highlight">mmm</span>ing
        </h1>
        <div className="App">
          <SearchBar onSearch={this.search} />
          <div className="App-playlist">
            <SearchResults searchResults={this.search} onAdd={this.addTrack} />
            <Playlist
              playlistName={this.state.playlistName}
              playlistTracks={this.state.playlistTracks}
              onRemove={this.removeTrack}
              onNameChange={this.updatePlaylistName}
              onSave={this.savePlaylist}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default App;
